function result(valid = true, message = "") {
    return { valid, result: message }
}

function safeLength(text = "", min = 3, max = 15, eMin = "", eMax = "") {
    let len = text.length;
    if (len < min) return result(false, eMin);
    else if (len > max) return result(false, eMax);
    return result(true);
}

function safeCharacters(text = "", err = "توجد أحرف/رموز غير صالحة") {
    let len = text.length;
    for (let i = 0; i < len; i++) {
        let c = text.codePointAt(i);
        // if true? then: unsafe character detected -> break loop
        if (c < 31 || (c > 127 && c < 1569) || (c > 1595 && c < 1601) || c > 1610) {
            return result(false, err);
        }
    }
    return result(true);
}

function safeText(text = "", min = 4, max = 20) {
    let result = "";
    let len = text.length;
    if (len < min) result = `الحد الأدنى ${min} أحرف`;
    else if (len > max) result = `الحد الأقصى ${max} حرف`;
    else {
        for (let i = 0; i < len; i++) {
            let c = text.codePointAt(i);
            // if true? then: unsafe character detected -> break loop
            if (c < 31 || (c > 127 && c < 1569) || (c > 1595 && c < 1601) || c > 1610) {
                result = "توجد أحرف/رموز غير صالحة";
                break;
            }
        }
    }
    return { valid: result === "", result };
}

function safeUsername(username = "") {
    let len = username.length;
    if (len < 3) return result(false, "أسم المستخدم قصير يجب ان لا يقل عن 3 أحرف");
    if (len > 15) return result(false, "أسم المستخدم طويل يجب ان لا يزيد عن 15 حرف");
    return safeCharacters(username);
}

function safeEmail(email = "") {
    let len = email.length;
    if (len < 5 || len > 32) return result(false, "بريد إلكتروني غير صالح");
    let valid = /(?:[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*|"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])*")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\[(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?|[a-z0-9-]*[a-z0-9]:(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21-\x5a\x53-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])+)\])/.test(email);
    if (!valid) return result(false, "بريد إلكتروني غير صالح");
    return safeCharacters(email, "بريد إلكتروني غير صالح");
}

function safePassword(password = "") {
    return safeLength(password, 5, 32,
        "كلمة المرور قصيرة يجب ان لا تقل عن 5 أحرف",
        "كلمة المرور طويلة يجب ان لا تزيد عن 32 حرف"
    );
}

export default { safeLength, safeText, safeUsername, safeEmail, safePassword, safeCharacters }