import { PureComponent } from 'react';
// import { TextField } from '@material-ui/core';
import { Form } from 'react-bootstrap';
import validate from "./Functions/validation";
import sharedMemory from './CLASSES/SharedMemory';
// import "./styles/feilds.css";

const FeildsData = {
    username: {
        value: "",
        isValid: () => false
    },
    email: {
        value: "",
        isValid: () => false
    },
    password: {
        value: "",
        isValid: () => false
    },
    init() {
        this.username.value = "";
        this.email.value = "";
        this.password.value = "";

        this.username.isValid = () => false;
        this.email.isValid = () => false;
        this.password.isValid = () => false;
    }
}

/** @augments {PureComponent<{ tabIndex: Number }>} */
class Username extends PureComponent {
    error = "";
    state = { validated: 0 } // 0=not validated yet, 1=not valid, 2=valid
    componentDidMount() {
        // console.log("Username did mount.");
        FeildsData.username.isValid = () => this.check();
    }
    check() {
        let value = FeildsData.username.value;
        let test = validate.safeUsername(value);
        console.log([value, test.valid, test.result]);
        this.error = test.result;
        this.setState({ validated: test.valid ? 2 : 1 });
        return test.valid;
    }
    render() {
        return <>
            <Form.Control tabIndex={this.props.tabIndex || 1} name="name" type="text" size="sm" onBlur={() => this.check()} onChange={e => (FeildsData.username.value = e.target.value)} placeholder="اسم المستخدم" />
            <div className="error ta-right">{this.error}</div>
        </>;
    }
}

