class SharedMemory {
    memory = {}
    set(name = "", data = null) {
        this.memory[name] = data;
    }
    get(name = "") {
        return this.memory[name];
    }
    run(name = "") {
        let data = this.memory[name];
        if (typeof data === "function")
            return data();
        else
            return null;
    }
}

const sharedMemory = new SharedMemory();

export default sharedMemory;