import { PureComponent } from 'react';
import Snackbar from '@material-ui/core/Snackbar';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import "./SnackBar.css";

const Dialog = {
    open: (msg = "") => { },
    close: () => { }
}

/** @augments {PureComponent<{ vertical: String, horizontal: String, duration: Number }>} */
class SnackBar extends PureComponent {
    state = { visible: false }
    message = "";
    constructor(props) {
        super(props);
    }
    componentDidMount() {
        Dialog.open = (msg = "") => this.open(msg);
        Dialog.close = () => this.close();
    }
    componentWillUnmount() {
        Dialog.open = (msg = "") => { };
        Dialog.close = () => { };
    }
    open(msg = "") {
        this.message = msg;
        this.setState({ visible: true });
    }
    close() { this.setState({ visible: false }); }

    render() {
        return <Snackbar
            anchorOrigin={{ vertical: this.props.vertical || "bottom", horizontal: this.props.horizontal || "center" }}
            open={this.state.visible}
            autoHideDuration={6000}
            message={this.message}
            onClose={() => this.close()}
            className="toast-message"
            action={
                <IconButton size="small" aria-label="close" color="inherit" onClick={() => this.close()}>
                    <CloseIcon fontSize="small" />
                </IconButton>
            }
        />
    }
}

export { SnackBar, Dialog }