import milliseconds from "./milliseconds";

export default function since(time = 0) {
    let passed = Date.now() - time;
    if (isNaN(passed)) return "...";
    //-------------------------------------
    if (passed > milliseconds(11))
        return Math.floor(passed / milliseconds(1)) + " يوم";
    else if (passed > milliseconds(3))
        return Math.floor(passed / milliseconds(1)) + " أيام";
    else if (passed > milliseconds(2))
        return "يومين";
    else if (passed > milliseconds(1))
        return "يوم واحد";
    //-------------------------------------
    else if (passed > milliseconds(0, 11))
        return Math.floor(passed / milliseconds(0, 1)) + " ساعة";
    else if (passed > milliseconds(0, 3))
        return Math.floor(passed / milliseconds(0, 1)) + " ساعات";
    else if (passed > milliseconds(0, 2))
        return "ساعتين";
    else if (passed > milliseconds(0, 1))
        return "ساعة واحدة";
    //-------------------------------------
    else if (passed > milliseconds(0, 0, 11))
        return Math.floor(passed / milliseconds(0, 0, 1)) + " دقيقة";
    else if (passed > milliseconds(0, 0, 3))
        return Math.floor(passed / milliseconds(0, 0, 1)) + " دقائق";
    else if (passed > milliseconds(0, 0, 2))
        return "دقيقتين";
    else if (passed > milliseconds(0, 0, 1))
        return "دقيقة واحدة";
    //-------------------------------------
    else if (passed > milliseconds(0, 0, 0, 11))
        return Math.floor(passed / 1000) + " ثانية";
    else if (passed > milliseconds(0, 0, 0, 3))
        return Math.floor(passed / 1000) + " ثواني";
    else if (passed > milliseconds(0, 0, 0, 2))
        return "ثانيتين";

    return "ثانية واحدة";
}